.button {
  width: 200px;
  height: 50px;
  border-radius: 50px;
  font-size: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.2s ease-in-out;
}

.button:hover {
  cursor: pointer;
  color: "white";
}

.button:active {
  transform: translateY(4px);
}


